function App() {

  const getSystemType = ()=>{
    return window.localStorage.getItem('systemType') || 'metric';
  }

  const setSystemType = (type)=>{
    window.localStorage.setItem('systemType',type);
    _setSystemType(type);
  }

  const [systemType, _setSystemType] = React.useState( getSystemType() );
  const [rows, _setRows] = React.useState([]);

  const _currency = (type, value, precision=2)=>(`${type === 'metric'?'€':'$'}${mCurrency.T[type](value, precision)}`);
  const _distance = (type, one=false)=>(`${type === 'metric'?`km${one?'':'s'}`:`mile${one?'':'s'}`}`);
  const _fVolume = (type, one=false)=>(`${type === 'metric'?`liter${one?'':'s'}`:`gallon${one?'':'s'}`}`);

  const headCells = [
    { id: 'id', numeric: true, label: 'ID' },
    { id: 'year', numeric: true, label: 'Year' },
    { id: 'name', numeric: false, label: 'Brand/Model' },
    { id: 'price', numeric: true, label: 'Price' },
    { id: 'capacityKwh', numeric: true, label: 'Usable cap.' },
    { id: 'range', numeric: true, label: 'Range' },
    { id: 'chargePrice', numeric: true, label: '1 Charge' },
    { id: 'travelPrice', numeric: true, label: 'Travel price' },
    { id: 'oneDistanceUnitPrice', numeric: true, label: 'Unit price' },
    { id: 'epaRange', numeric: true, label: 'EPA-Range' },
    // "id": 0,
    // "name": "Mazda MX-30",
    // "year": 2022,
    // "price": 34645,
    // "capacityKwh": 32.97,
    // "range": 160.93,
    // "epaRange": 1.7,
    // "chargePrice": 9.89,
    // "oneDistanceUnitPrice": 0.0615,
    // "epaFuelEconomy": "combined/city/autobahn: 39.11/41.66/36.14",
    // "description": "The adorable Mazda MX-30 is about the same size as its gas-powered subcompact crossover CX-30 stablemate, but it uses a 143-hp front-wheel-drive electric powertrain and unlike the CX-30, is not offered with all-wheel drive. Despite this electric car's limited range, the MX-30 is one of the more stylish choices. It uses rear-hinged back doors and a floating center console to maximize space in the cockpit. It has a 30.0-kWh battery pack, which contributes to its lower EPA-estimated 100 miles of range, but Mazda did promise a plug-in-hybrid version will arrive later in 2022 with better range. ",
    // "image": "https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/2022-mazda-mx30-01-1633gg-1637209237.jpeg",
    // "epaMaps": {
    //   "combined": 39.11,
    //   "city": 41.66,
    //   "highway": 36.14
    // },
    // "travelPrice": {
    //   "amount": 100,
    //   "price": 6.15
    // },
    // "unitPrice": {
    //   "vehicle": 215.28
    // },
    // "links": {
    //   "info": "https://www.caranddriver.com/mazda/mx-30",
    //   "specs": "https://www.caranddriver.com/mazda/mx-30/specs"
    // },
  ];

  React.useEffect(()=>{
    const rows = JSON.parse(JSON.stringify(mData[ systemType ])).map((mRec)=>{

      for( const mKey of Object.keys(mRec) )
        mRec[`${mKey}_raw`] = mRec[ mKey ];

      mRec.epaMaps_raw = mRec.epaMaps.combined;
      mRec.travelPrice_raw = mRec.travelPrice.price;
      mRec.unitPrice_raw = mRec.unitPrice.vehicle;

      mRec.epaMaps = `${mRec.epaMaps.combined}/${ _fVolume(systemType) }`;
      mRec.travelPrice = `${mRec.travelPrice.amount}${_distance(systemType)}/${_currency(systemType, mRec.travelPrice.price)}`;
      mRec.unitPrice = _currency(systemType, mRec.unitPrice.vehicle);
      mRec.chargePrice = _currency(systemType, mRec.chargePrice);
      mRec.price = _currency(systemType, mRec.price);
      mRec.capacityKwh = `${mRec.capacityKwh} kWh`;
      mRec.range = `${mRec.range}/${_distance(systemType)}`;
      mRec.oneDistanceUnitPrice = `${_currency(systemType, mRec.oneDistanceUnitPrice, 4)}/${_distance(systemType,true)}`;
      mRec.epaRange = `${mRec.epaRange}/${_distance(systemType)}/${_fVolume(systemType,true)}`;
      mRec.image = `/images/vehicles/${ mRec.image_raw.split('/').pop().trim().replace('.png','.jpg') }`;

      const row = {};
      headCells.map((mHead)=>{
        row[ mHead.id ] = mRec[ mHead.id ];
        row[`${mHead.id}_raw`] = mRec[`${mHead.id}_raw`];
      });

      row.isExpanded = false;

      row.Info = ()=>{
        return (
          <div style={{}}>
            <div style={{width: '30%', boxSizing: 'border-box', padding: '8px 14px', display: 'inline-block', verticalAlign: 'top'}}>
              <img style={{width: '100%', border: 'solid 4px #fff', boxShadow: '0 0 4px #333'}} src={mRec.image} />
            </div>
            <div style={{width: '70%', boxSizing: 'border-box', padding: 8, display: 'inline-block', verticalAlign: 'top'}}>
              <div style={{fontSize: 20, margin: '10px auto'}}>{mRec.year_raw} - {mRec.name_raw} @ {_currency(systemType, mRec.price_raw)}</div>
              <div style={{color: '#333', margin: '10px auto'}}>{mRec.description_raw}</div>
              {Object.keys(mRec.links_raw).map((mKey, mLinkIndex)=>(
                <div key={`rec-${mRec.id}-link-${mLinkIndex}`} style={{color: '#333', margin: '5px auto'}}>
                  <span style={{textTransform: 'capitalize'}}>{mKey}</span>: 
                  <a target="_blank" href={`${mRec.links_raw[ mKey ]}?source=${location.hostname}${location.pathname}`}>{mRec.links_raw[ mKey ]}</a>
                </div>
              ))}
            </div>
          </div>
        );
      };

      return row;
    });

    _setRows(rows);

  }, [ systemType ]);

  return (

    <Container maxWidth="xl">

      <header className="App-header" style={{textAlign: 'center', margin: '30px auto', fontSize: 24}}>
        Comparing 40 EVs of 2022
      </header>

      <section className="App-section" style={{margin: '10px auto 20px auto', textAlign: 'center'}}>

        <SystemSelector 
          // options={ systemType === 'metric' ? ['metric','imperial'] : ['imperial','metric'] }
          options={ ['metric','imperial'] }
          initialSelectedIndex={ ['metric','imperial'].indexOf(systemType) }
          onSelect={(systemType)=>{
            console.log(`onSelect: ${systemType}`);
            setSystemType(systemType);
          }}
        />

      </section>

      <section className="App-section">

        <MTable 
          initialOrder={'asc'}
          initialOrderBy={'price_raw'}

          headCells={headCells}
          rows={rows}
          showPagination={false}
          />

      </section>

      <footer className="App-footer" style={{textAlign: 'center', margin: '30px auto'}}>
        {window.location.href}
      </footer>

    </Container>
  );
}

export default App;
