import currency from 'currency.js';

const USD = (value, precision=2) => currency(value, { /*symbol: '$',*/ symbol: '', precision }).format();
const EURO = (value, precision=2) => currency(value, { /*symbol: '€',*/ symbol: '', precision }).format();
const JPY = (value, precision=2) => currency(value, { /*symbol: '¥',*/ symbol: '', precision }).format();
const toCents = (value=0.00) => (Math.floor(value*100));

const T = {
  imperial: USD,
  metric: EURO,
};

export default {
  T,
  USD,
  EURO,
  JPY,
  toCents,
  lib: currency,
}

